@import 'variables';

/*BLOCK COPYRIGHT*/

.copyright__text {
    font-family: 'Arial', sans-serif;
    font-size: 16px;
    line-height: 16px;
    color: #444444;
    cursor: pointer;

    &:active {
        opacity: 0.3;
    }
}

.copyright__picture {
    width: 27px;
    height: 34px;
    text-decoration: none;
}
.copyright__image {
    position: relative;
    cursor: pointer;
    color: transparent;

    &::before {
        content: "";
        position: absolute;

        top: 0px;
        left: 0;
        width: 27px;
        height: 34px;

        background: url(../img/htmlacademy.png);
    }

    &:hover::before {
        background: url(../img/htmlacademy-hover.png);
    }

    &:active::before {
        background: url(../img/htmlacademy-hover.png);
        opacity: 0.3;
    }
}

@media (min-width: @tablet-width) {
    .copyright__text {
       margin-right: 20px;
    }
}
