@import 'variables';

/*BLOCK PROCESS*/
.process {
    display: flex;
    flex-direction: column;

    width: 280px;

    margin: 0 auto;
    margin-top: 20px;
    padding: 0 20px;
}

.process__title {
    margin: 0;
    padding: 0;
}

.process__list {
    margin: 0;
    margin-top: 30px;
    padding: 0;
}

.process__item {
    position: relative;
    display: block;
    list-style-type: none;
    margin-bottom: 20px;
    padding-left: 80px;
}

.process__item::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 80px;
    height: 80px;
    background-repeat: no-repeat;
}

.process__item--nutrition::after {
    background-image: url(../img/advantage-1.png);
}

.process__item--powder::after {
    background-image: url(../img/andvantage-2.png);
}

.process__item--eating::after {
    background-image: url(../img/advantage-3.png);
}

.process__item--time::after {
    background-image: url(../img/advantage-4.png);
}
.process__description {
    margin: 0;
    padding: 0;

    font-family: 'Arial', sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #444444;
}

.process__number {
    display: none;
}


@media (min-width: @tablet-width) {
    .process {
        width: 708px;

        margin: 0 auto;
        margin-top: 20px;
        padding: 0 30px;
    }

    .process__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .process__item {
        position: relative;
        width: 50%;
        height: 148px;

        margin-bottom: 88px;
        padding: 0;
        padding-top: 80px;
    }

    .process__number {
        position: absolute;
        z-index: 1;
        top: -39px;
        left: 125px;

        display: block;

        font-family: 'Oswald', sans-serif;
        font-size: 281px;
        line-height: 281px;
        color: #f2f2f2;
    }

    .process__description {
        position: relative;
        z-index: 2;
        width: 245px;
        font-size: 16px;
        line-height: 24px;
    }

    .site-title {
        font-size: 60px;
    }
}


@media (min-width: @desktop-width) {

    .process {
        width: 1220px;

        margin: 0 auto;
        margin-top: 20px;
        padding: 0 30px;
    }

    .process__list {
        width: 1220px;
        justify-content: space-between;
    }

    .process__item {
        width: 21%;
    }

}