@import 'variables';

img {
    width: 100%;
    height: auto;
}

.svg-icon {
    display: none;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 60px;
    height: 32px;
    fill: none;
    stroke: #666666;
    stroke-width: 20px;
}

.visually-hidden {
    display: none;
}

.visually-hidden-button {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    background-color: red;
    
    white-space: nowrap;

    clip-path: inset(100%);
    clip: rect(0 0 0 0) ;
    overflow: hidden;
}

.button {
    padding: 12px 0;

    font-family: 'Oswald', sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #ffffff;

    background-color: #68b738;
    
    text-decoration: none;
    text-transform: uppercase;
    cursor: pointer;

    &:hover {
        background-color: #5eaa2f;
    }

    &:active {
        color: #8fc46e;
    }
}

.decoration-line {
    position: absolute;
    z-index: 1;

    top: 43px;
    right: 0px;
    width: 53px;
    height: 3px;
    background-color: #68b738;
}

.big-heading .decoration-line {
    top: 12px;
    width: 320px;
}

.h2-title {
    position: relative;
    z-index: 2;

    display: inline-block;
    margin: 0;
    padding: 0;
    padding-left: 20px;
    padding-right: 24px;

    font-size: 24px;
    line-height: 24px;
    font-weight: 400;
    
    vertical-align: top;
    background-color: #ffffff;
}

.site-title {
    font-size: 36px;
    line-height: 60px;
    font-weight: 400;
}


@media (min-width: @tablet-width) {
    .decoration-line {
        top: 50px;
        width: 768px;
    }

    .big-heading .decoration-line {
        top: 20px;
        width: 768px;
    }
}

@media (min-width: @desktop-width) {

    .decoration-line {
        top: 53px;
        width: 1440px;
    }

    .big-heading .decoration-line {
        top: 20px;
        width: 1440px;
    }
}

