@import 'variables';

/*BLOCK CATALOG*/
.catalog {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.catalog__good {
    display: flex;
    flex-direction: column;
    width: 320px;
    padding-bottom: 24px;

    border-bottom: 1px solid #e6e6e6;

}

.catalog__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;

    padding: 0 20px;
}

.catalog__wrapper--2 {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.catalog__pic {
    width: 140px;
}

.catalog__pic--plus {
    width: 30px;
    height: 30px;
    align-self: center;
}

.catalog__good-info {
    width: 140px;
    display: flex;
    flex-direction: column;
}

.catalog__good-info--2 {
    width: 100%;
    margin-top: 24px;
}

.catalog__name {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 20px;
}

.catalog__name--2 {
    text-align: center;
    text-transform: uppercase;
}

.catalog__small-text {
    display: block;

    margin-top: 10px;
    font-family: 'Arial', sans-serif;
    font-size: 12px;
    line-height: 16px;
    color: #444444;

    text-align: center;
}


.catalog__stats-list {
    margin: 0;
    padding: 0;
    margin-top: 10px;
    font-family: 'Arial', sans-serif;
    font-size: 12px;
    line-height: 16px;
    color: #444444;

}
.catalog__stats-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 140px;
}

.catalog__button-wrapper {
    width: 280px;
    padding: 0 20px;
}

.catalog__button {
    display: block;

    font-size: 16px;
    line-height: 20px;

    text-align: center;

}

.catalog__button--2 {
    margin-top: 23px;

    color: #444444;

    background-color: #f2f2f2;

    text-transform: uppercase;

    &:hover {
        background-color: #ebebeb;
    }

    &:active {
        color: #b9b9b9;
    }
}
/*BLOCK CATALOG*/


@media (min-width: @tablet-width) {

    .catalog {
        width: 708px;
        margin-top: 140px;
        padding: 0 30px;
        justify-content: space-between;
    }

    .catalog__good {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        width: 250px;
        min-height: 391px;

        padding: 0 37px;
        padding-bottom: 40px;
        margin-bottom: 140px;

        border: none;
        background-color: #f2f2f2;

    }

    .catalog__good--more {
        border: 1px solid #e6e6e6;
        background-color: #ffffff;
    }

    .catalog__wrapper {
        display: flex;
        flex-direction: column;
        padding: 0;
    }


    .catalog__pic {
        width: 100%;
    }

    .catalog__image {
        display: none;
    }

    .catalog__image-tablet::before {
        position: absolute;
        content: "";

        background-repeat: no-repeat;
    }

    .catalog__image-tablet--plus::before {
        top: 54px;
        left: 130px;

        width: 60px;
        height: 60px;

        background: url(../img/plus.png);
    }

    .catalog__image-tablet--1::before {
        top: -43px;
        left: 89px;

        width: 161px;
        height: 172px;
    }

    .catalog__image-tablet--2::before {
        top: -75px;
        left: 89px;

        width: 195px;
        height: 208px;
    }

    .image-1::before {
        background: url(../img/catalog-1-tablet.png);
    }


    .image-2::before {
        background: url(../img/catalog-2-tablet.png);
    }

    .image-3::before {
        background: url(../img/catalog-3-tablet.png);
    }

    .image-4::before {
        background: url(../img/catalog-4-tablet.png);
    }

    .image-5::before {
        background: url(../img/catalog-5-tablet.png);
    }

    .image-6::before {
        background: url(../img/catalog-6-tablet.png);
    }

    .image-7::before {
        background: url(../img/catalog-7-tablet.png);
    }

    .catalog__good-info {
        width: 100%;
    }

    .catalog__name {
        text-align: center;
    }

    .catalog__stats-list {
        width: 250px;
        font-size: 16px;

    }
    .catalog__stats-item {
        width: 100%;
        padding: 10px 0;

        border-bottom: 1px solid #cdcdcd;
    }
    .catalog__button-wrapper {
        width: 100%;

        margin-top: 34px;
        padding: 0;
    }

}

@media (min-width: @desktop-width) {
    .catalog {
        width: 1220px;
        padding: 0 110px;
    }

    .catalog__good {
        width: 169px;
        padding: 0 38px;
        padding-bottom: 40px;
    }
    .catalog__stats-list {
        width: 169px;
        font-size: 16px;

    }

    .catalog__image-tablet--1::before {
        left: 42px;
    }

    .catalog__image-tablet--2::before {
        left: 30px;
    }

    .catalog__image-tablet--plus::before {
        left: 90px;
    }
}
