/*BLOCK PROGRAMS*/
@import 'variables';

.programs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;

    width: 280px;
    margin: 0 auto;
}

.programs__type {
    display: flex;
    flex-direction: column;
    
    margin-bottom: 20px;
    padding: 24px 20px;

    background-color: #f2f2f2;
}

.programs__type:last-child {
    margin-bottom: 0;
}

.programs__image {
    display: none;
}

.programs__pic-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;

    margin-bottom: 20px;
}

.programs__title {
    margin: 0;
    padding: 0;

    font-size: 24px;
    line-height: 37px;
    font-weight: 400;

    text-transform: uppercase;
}

.programs__title--slim {
    padding-left: 20px;
}

.programs__title--pro {
    padding-left: 13px;
}

.programs__description {
    margin: 0;
    padding: 0;
    padding-bottom: 20px;

    font-family: 'Arial', sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #444444;
    
    border-bottom: 1px solid #d9d9d9;
}

.programs_catalog {
    margin-top: 20px;
}

.programs__to-catalog {
    position: relative;
    display: inline-block;

    padding-right: 36px;

    font-size: 16px;
    line-height: 16px;
    color: #000000;

    vertical-align: top;
    text-decoration: none;
    text-transform: uppercase;

    &::after {
        content: "";
        position: absolute;
        top: 2px;
        right: 0;
        width: 24px;
        height: 11px;
    
        background: url(../img/right-arrow-long.png) no-repeat;
    }

    &:active {
        opacity: 0.3;
    }
}





@media (min-width: @tablet-width) {
    .programs {
        width: 708px;
    }
    
    .programs__type {
        flex-direction: row;

        padding-top: 80px;
        padding-bottom: 60px;
        padding-right: 28px;
        padding-left: 55px;
    }

    .programs__pic {
        display: none;
    }

    .programs__image {
        display: block;
        padding-left: 15px;
    }

    .programs__image--slim {
        padding-right: 36px;
    }
    
    .programs__title {
        padding: 0;
        font-size: 36px;
    }
    
    .programs__description {
        padding-bottom: 0;
        font-size: 16px;
        line-height: 24px;

        border-bottom: 0px solid #d9d9d9;
    }

    .programs_catalog {
        margin-top: 60px;
    }

    .programs__to-catalog {
       font-size: 20px;
       line-height: 20px;

       &::after {
        top: 5px;
        }
    }

}



@media (min-width: @desktop-width) {
    .programs {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
    
        width: 1220px;
    }

    .programs__type {
        width: 465px;
        height: 272px;
        padding: 0;
        padding-top: 40px;
        padding-bottom: 60px;
        padding-right: 55px;
        padding-left: 50px;
    }
    
    .programs__pic {
        display: block;
    }

    .programs__image {
        display: none;
    }

    .programs__title--slim {
        padding-left: 64px;
    }

    .programs__title--pro {
        padding-left: 48px;
    }
}
