@import 'variables';
/*BLOCK EXAMPLE*/
.example {
    display: flex;
    flex-direction: column;

    width: 280px;

    margin: 0 auto;
    margin-top: 20px;
    padding: 0 20px;
    padding-bottom: 35px;

    background-color: #f2f2f2;
}

.example-title {
    margin: 0;
    padding: 0;
}

.example__text {
    margin: 0;
    padding: 0;

    font-family: 'Arial', sans-serif;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    color: #444444;
}

.example__result {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    margin: 0;
    margin-top: 25px;
    padding: 0;


    &::before {
        content: "";
        position: absolute;
        top: -16px;
        left: 0px;

        width: 124px;
        height: 56px;

        border: 1px solid #cdcdcd;
        border-radius: 5px;
    }

    &::after {
        content: "";
        position: absolute;
        top: -16px;
        left: 150px;

        width: 124px;
        height: 56px;

        border: 1px solid #cdcdcd;
        border-radius: 5px;
    }

}

.result__how-many {
    position: relative;
    z-index: 10;
    margin: 0;
    padding: 0 15px;

    font-size: 12px;
    line-height: 12px;
    font-family: 'Arial', sans-serif;
    color: #444444;
    text-align: center;
    background-color: #f2f2f2;

    &:last-child {
        margin-left: 25px;
        padding: 0 5px;
    }


}
.result__number {
    display: block;
    margin: 0;
    margin-bottom: 10px;
    padding: 0;
    font-size: 24px;
    line-height: 24px;
    font-family: 'Oswald', sans-serif;
    color: #000000;
}

.example__cost {
    margin: 0;
    margin-top: 20px;
    padding: 0;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
}

.example__difference {
    display: flex;
    flex-direction: column;
    margin-top: 45px;
}


/*BLOCK EXAMPLE*/

/*BLOCK DIFFERENCE*/
.difference__photo {
    position: relative;
    left: -15px;
}

.difference__controls {
    display: flex;
    flex-direction: row;
    align-items: center;

    justify-content: center;
    margin: 0;
    margin-top: 30px;
    padding: 0;
}

.difference__photo--tablet {
    display: none;
}
/*BLOCK DIFFERENCE*/


/*BLOCK CONTROLS*/
.controls__btn {
    font-family: 'Oswald', sans-serif;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    background-color: transparent;
}

.controls__btn--before {
    margin-right: 10px;
}

.controls__btn--after {
    margin-left: 10px;
}

.controls__range {
    position: relative;
    width: 84px;
    height: 14px;
}

.controls__scale {
    position: relative;
    z-index: 1;
}
.controls__bar {
    width: 100%;
    height: 14px;
    background-color: #ffffff;
    border: 1px solid #cdcdcd;
    border-radius: 5px;
}

.controls__range-toggle {
    position: absolute;
    z-index: 2;
    top: 5px;
    left: 5px;
    width: 35px;
    height: 6px;
    background-color: #68b738;
    border-radius: 3px;
}
/*BLOCK CONTROLS*/

@media (min-width: @tablet-width) {
    .controls__btn {
        font-size: 20px;
        line-height: 20px;
        text-decoration: dotted;
    }

    .controls__range {
        width: 428px;
        height: 6px;
    }

    .controls__bar {
        width: 100%;
        height: 6px;
        background-color: #dcdcdc;
        border: none;
        border-radius: 3px;
    }

    .controls__range-toggle {
        position: absolute;
        z-index: 2;
        top: -15px;
        left: 214px;
        width: 34px;
        height: 34px;
        background-color: #ffffff;
        border: 1px solid #cdcdcd;
        border-radius: 50%;

        &::before {
            content: "";
            position: absolute;
            top: 13px;
            left: 13px;
            width: 8px;
            height: 8px;

            background-color: #68b738;
            border-radius: 50%;
        }
    }
}


@media (min-width: @desktop-width) {
    .controls__btn {
        position: relative;

        &::after {
            content: "";
            position: absolute;
            bottom: -5px;
            left: 10px;
            height: 1px;

            background: url(../img/btn-dotted.png) no-repeat;
        }
    }

    .controls__btn--before {

        &::after {
            left: 7px;

            width: 45px;

            background: url(../img/btn-dotted--before.png) no-repeat;
        }
    }

    .controls__btn--after {
         &::after {
            left: 7px;
            width: 49px;
            background: url(../img/btn-dotted--after.png) no-repeat;
        }
    }
}


@media (min-width: @tablet-width) {
    .example {
        width: 708px;
        padding: 0 30px;
        padding-bottom: 65px;
        background-color: #ffffff;
    }

    .example__result-cost {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .example__result {
        width: 366px;
        justify-content: space-between;
    }


    .example__result {
        &::before {
            content: "";
            position: absolute;
            top: -16px;
            left: 0px;

            width: 162px;
            height: 72px;

            border: 1px solid #cdcdcd;
            border-radius: 5px;
        }

        &::after {
            content: "";
            position: absolute;
            top: -16px;
            left: 204px;

            width: 162px;
            height: 72px;

            border: 1px solid #cdcdcd;
            border-radius: 5px;
        }

    }

    .result__how-many {
        width: 114px;
        margin: 0;
        margin-left: 25px;
        padding: 0;
        font-size: 14px;
        line-height: 14px;
        background-color: #ffffff;

        &:last-child {
          width: 142px;
          margin-right: 8px;
          padding: 0;
        }
        & br {
            display: none;
        }
    }

    .result__number {
        font-size: 30px;
        line-height: 37px;
    }

    .example__cost {
        width: 180px;
        font-size: 20px;
        text-align: right;
    }

    .example__text {
        font-size: 16px;
        line-height: 24px;
    }

    .example__difference {
        align-items: center;
    }
}



@media (min-width: @desktop-width) {
    .example {
        display: flex;
        flex-direction: row;

        width: 1220px;

        margin: 0 auto;
        margin-top: 20px;
        padding: 0 110px;
        padding-bottom: 60px;
        background-color: #f2f2f2;
    }
    .example__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .example__result-cost {
        flex-direction: column;
        align-items: flex-start;
    }

    .example__result {
        margin-bottom: 55px;
    }

    .result__how-many {
        background-color: #f2f2f2;
    }

    .example__cost {
        width: auto;
    }

    .example__difference {
        margin-top: 0;
    }

    .difference__controls {
        margin-top: 0;
    }

}
