@import 'variables';

.page-form {
    display: flex;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
}
.page-form__title {
    margin: 0;
    padding: 0;
    padding-left: 20px;
    padding-top: 18px;

    border-top: 1px solid #e6e6e6;
}

.page-form__text {
    margin: 0;
    margin-top: 22px;
    margin-bottom: 16px;
    padding: 0;
    padding-left: 20px;

}


@media (min-width: @tablet-width) {
    .page-form {
        width: 768px;
    }

    .page-form__title {
        padding-left: 30px;
        border-top: 0px solid #e6e6e6;
    }

    .page-form__text {
        margin: 0;
        margin-top: 50px;
        padding-left: 70px;

        font-size: 20px;
        line-height: 30px;

    }
}



@media (min-width: @desktop-width) {
    .page-form {
        width: 1440px;
    }

    .page-form__title {
        padding-left: 110px;
    }

    .page-form__text {
        padding-left: 110px;


    }
}
