@import 'variables';

/*BLOCK SOCIAL*/
.social__list {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    width: 280px;
    margin: 0;

    padding: 30px 0;
}

.social__items {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.social__link {
    position: relative;
    color: transparent;
}

.social__link--vk {
    margin-left: 40px;
}

.social__link--fb {
    margin-right: 55px;
}

.social__link::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    background-repeat: no-repeat;
}

.social__link--vk {

    &::before {
    top: 5px;
    left: 0;
    width: 30px;
    height: 16px;
    background: url(../img/vk.png);
    }

    &:hover::before {
        background: url(../img/vk-hover.png);
    }

    &:active::before {
        background: url(../img/vk-hover.png);
        opacity: 0.3;
    }
}

.social__link--fb {

    &::before {
        top: -1px;
        left: 0;
        width: 14px;
        height: 28px;
        background: url(../img/fb.png);
    }

    &:hover::before {
        background: url(../img/fb-hover.png);
    }

    &:active::before {
        background: url(../img/fb-hover.png);
        opacity: 0.3;
    }
}


.social__link--instagram {

    &::before {
        top: 0;
        left: 8px;
        width: 23px;
        height: 23px;
        background: url(../img/insta.png);
    }

    &:hover::before {
        background: url(../img/insta-hover.png);
    }

    &:active::before {
        background: url(../img/insta-hover.png);
        opacity: 0.3;
    }
}
