@import 'variables';

/*BLOCK PAGE-FOOTER*/
.page-footer {
    display: flex;
    flex-direction: column;
}

.page-footer__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    width: 280px;
    margin: 0 auto;
    margin-top: -5px;
    padding: 0 20px;
    padding-top: 36px;
    padding-bottom: 20px;
    background-color: #f2f2f2;
}

.page-footer__footer-name {
    display: block;
    padding-bottom: 30px;

    font-size: 28px;
    line-height: 28px;

    text-align: center;

    border-bottom: 1px solid #d9d9d9;
    cursor: pointer;

    &:hover {
        opacity: 0.8;
    }

    &:active {
        opacity: 0.6;
    }
}

.page-footer__social {
    border-bottom: 1px solid #d9d9d9;
}

.page-footer__copyright {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 0;
    padding-bottom: 0;
    align-items: center;
}
/*BLOCK PAGE-FOOTER*/

@media (min-width: @tablet-width) {
    .page-footer__wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 708px;
        height: 150px;
        margin: 0 auto;
        margin-top: 0px;
        padding: 0 30px;

        background-color: #f2f2f2;
    }

    .page-footer__footer-name {
        padding-bottom: 0;
        font-size: 28px;
        line-height: 28px;

        border-bottom: 0px solid #d9d9d9;
    }

    .page-footer__copyright {
        padding: 0;
    }

    .page-footer__social {
        border-bottom: 0px solid #d9d9d9;
    }
}


@media (min-width: @desktop-width) {
    .page-footer__wrapper {
        width: 1220px;
        padding: 0 110px;
    }
}

/*BLOCK SOCIAL*/
