@import 'variables';

/*BLOCK PAGE-CATALOG*/
.page-catalog {
    display: flex;
    flex-direction: column;

}

.page-catalog__wrapper {
    width: 320px;
    margin: 0 auto;
}

.page-catalog__title {
    margin: 0;
    padding: 0;
    padding-left: 20px;
    padding-top: 18px;
    padding-bottom: 30px;

    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
}
/*BLOCK PAGE-CATALOG*/



@media (min-width: @tablet-width) {
    .page-catalog__wrapper {
        width: 768px;
        margin: 0 auto;
    }

    .page-catalog__title {
        padding-left: 110px;
    }
    
}



@media (min-width: @desktop-width) {
    .page-catalog__wrapper {
        width: 1440px;
        margin: 0 auto;
    }

    .page-catalog__title {
        border: none;
    }
    
}







