@import 'variables';

/*BLOCK LOCATION*/

.location {
    position: relative;
    display: flex;
    flex-direction: column;

    width: 320px;

    margin: 0 auto;
    margin-top: 70px;
}

.location__address {
    position: absolute;
    top: -65px;
    left: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    width: 280px;
    padding: 20px 20px;
    background-color: #ffffff;
}

.location__address--catalog {
    background-color: #f2f2f2;
}

.location__to-dealers {
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
}

.location__map {
  width: 320px;
  height: 400px;
}

.location__street {
    font-family: 'Arial', sans-serif;
}


@media (min-width: @tablet-width) {
    .location {
        width: 768px;
        margin-top: 100px;
    }

    .location__address {
        top: -100px;
        left: 0;
        width: 628px;
        padding: 20px 70px;
    }

    .location__to-dealers {
        font-size: 20px;
        line-height: 25px;
        text-transform: uppercase;
    }

    .location__map {
      width: 767px;
      height: 400px;
    }

    .location__street {
        font-family: 'Arial', sans-serif;
    }
}



@media (min-width: @desktop-width) {

    .location {
        width: 1440px;
        margin-top: 0px;
    }

    .location__address {
        top: 95px;
        left: 110px;
        width: 393px;
        height: 201px;
        padding: 0 77px;
        background-color: #ffffff;
    }

    .location__map {
      width: 1440px;
      height: 400px;
    }
}
