@import 'variables';

/*BLOCK LIST*/
.list {
    width: 280px;

    margin: 0;
    padding: 0;


    font-family: 'Arial', sans-serif;
    font-size: 14px;
    line-height: 14px;
    color: #444444;

    list-style-type: none;
}

.list__item {
    display: block;
}

.list__item--1 {
    padding-bottom: 16px;

    font-family: 'Oswald', sans-serif;
    font-size: 16px;
    line-height: 16px;

    text-transform: uppercase;
}

.list__item--2 {
    margin-top: 14px;
}

.list__table-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 280px;
}


.order__button {
    display: block;
    width: 280px;

    font-size: 16px;
    line-height: 20px;

    text-align: center;
}
/*BLOCK CATALOG*/


@media (min-width: @tablet-width) {

    .list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        width: 636px;

        padding: 0 36px;
    
        font-size: 16px;
        line-height: 16px;
    
        list-style-type: none;
    }

    .list__table-item {
        width: 230px;
    }

    .list__btn {
        width: 636px;
        padding: 0 36px;
    }

    .order__button {
        width: 100%;
        line-height: 16px;
    }
}



@media (min-width: @desktop-width) {
    
    .list {
        display: flex;
        justify-content: space-between;

        width: 610px;
    }
    
    .list__item--1 {
        padding: 0;
        font-size: 20px;
        line-height: 20px;
    }

    .list__table-item {
        width: 285px;
    }

    .list__btn {
        width: 245px;
        padding: 0;
    }

}