@import 'variables';

form {
    width: 320px;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form__wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom:30px;
}

.form__data {
    width: 320px;
    padding-bottom: 23px;
}

.form__title {
    width: 184px;
}

.form__big-heading {
    position: relative;
    width: 100%;
}

.form__data--button {
    display: flex;
    flex-direction: column;

    margin-bottom: 20px;
    padding: 0 20px;
}

.form__data--bottom-line {
    border-bottom: 1px solid #ededed;
}

.form__button {
    width: 280px;
    border: none;
}

.form__legend {
    align-self: center;

    margin-top: 20px;
    margin-right: 30px;

    font-family: 'Arial', sans-serif;
    font-size: 14px;
    line-height: 16px;
    color: #444444;
    
}



@media (min-width: @tablet-width) {
    form {
        width: 768px;
    }

    .form__wrapper {
        display: flex;
        flex-direction: column;  
        margin-top: 40px;
    }

    .form__wrapper-2 {
        margin-top: 60px;
    }

    .form__wrapper-3 {
        width: 630px;
    
        margin: 0;
        margin-top: 20px;
    }
   

    .form__data {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 768px;
        margin-bottom:30px;
    }

    .form__data--bottom-line {
        border: none;
    }

    .form__title {
        width: auto;
    }

    .form__big-heading {
        align-self: flex-start;
    }

    .form__data--button {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 630px;
        margin-bottom: 20px;
        padding: 0 70px;
    }
    
    .form__button {
        width: 283px;
        margin-right: 60px;
    }
    
    .form__legend {    
        align-self: auto;

        margin: 0;

        font-size: 16px;
        color: #444444;
        
    }

    .form__data--bottom-line {
        border-bottom: 0px solid #ededed;
    }
    
    .form__button {
        width: 280px;
        border: none;
    }
    

    .h2-title {
        margin-left: 30px;
        padding-left: 39px;
        padding-right: 44px;
    
        font-size: 36px;
        line-height: 36px;
    }

}

@media (min-width: @desktop-width) {
    form {
        width: 1440px;
    }

    .form__wrapper {
        display: flex;
        flex-direction: row; 
        justify-content: space-between;
        align-items: center;
        width: 1220px;
        padding: 0 110px;
    }

    .form__wrapper-2 {
        display: flex;
        flex-direction: column; 
        justify-content: space-between;
        width: 1220px;
        margin-top: 60px;
        padding: 0 110px;

    }

    .form__wrapper-3 {
        width: 1220px;
        margin-top: 50px;
        padding-left: 110px;
    }

    .form__data {
        width: 1440px;
        align-items: flex-start;
    }

    .form__data--1 {
        width: 570px;
    }

    .form__data--button {
        width: 1220px;
        align-items: center;
        margin-bottom: 20px;
        padding: 0 110px;
    }
    
    .form__button {
        width: 570px;
        margin-right: 80px;
    }

    .h2-title {
        margin-left: 70px;
    }
}

