@import 'variables';

/*BLOCK CATALOG*/
.add-catalog {
    display: flex;
    flex-direction: column;
}


.add-catalog__big-heading {
    position: relative;
    padding: 31px 0;
}

.add-catalog__goods {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
}

.add-catalog__list {
    display: flex;
    flex-direction: column;
    padding: 12px 0;
    border-top: 1px solid #ededed;
}

.add-catalog__list--1 {
    margin-bottom: 40px;
    border-bottom: 1px solid #ededed;
}

.add-catalog__gift {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 280px;
    min-height: 289px;

    margin-bottom: 30px;
}

/*BLOCK CATALOG*/

@media (min-width: @tablet-width) {
    .add-catalog__goods {
        padding: 0 30px;
    }

    .add-catalog__list {
        justify-content: space-around;
        height: 138px;
        border: 1px solid #d9d9d9;
    }

    .add-catalog__gift {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;

        width: 708px;
        min-height: 200px;

        margin-bottom: 30px;
    }


}

@media (min-width: @desktop-width) {

    .add-catalog__goods {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0 110px;
    }

    .add-catalog__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 895px;
        height: auto;
        padding: 0;
        padding-top: 15px;
        padding-bottom: 16px;
        border: none;
        border-top: 1px solid #ededed;
    }

    .add-catalog__gift {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        width: 245px;
        min-height: 289px;

        margin-bottom: 30px;
    }

}

/*BLOCK GIFT*/

.gift {
    background: url(../img/gift-photo-mobile.jpg) no-repeat;

    @media (min-resolution: @retina-dpi), (min-resolution:@retina-dppx) {
        background: url(../img/gift-photo-mobile@2x.jpg);
        background-size: 280px auto;
    }
}

.gift__text {
    margin: 0;
    margin-top: 70px;
    padding: 0 60px;
    font-family: 'Arial', sans-serif;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    text-align: center;
}

.gift__wrapper {
    width: 55px;
    height: 60px;
}


@media (min-width: @tablet-width) {

    .gift {
        background: url(../img/gift-photo-tablet.jpg) no-repeat;

        @media (min-resolution: @retina-dpi), (min-resolution:@retina-dppx) {
            background: url(../img/gift-photo-tablet@2x.jpg);
            background-size: 708px auto;
        }
    }


    .gift__text {
        width: 150px;
        margin: 0;
        padding: 0;
        text-align: left;
    }
}


@media (min-width: @desktop-width) {

    .gift {
        background: url(../img/gift-photo-desktop.jpg) no-repeat;

        @media (min-resolution: @retina-dpi), (min-resolution:@retina-dppx) {
            background-image: url(../img/gift-photo-desktop@2x.jpg);
            background-size: 245px auto;
        }
    }

    .gift__text {
        margin-top: 50px;
        padding: 0 40px;
        color: #ffffff;
        text-align: center;
    }
}
