@import 'variables';

/*BLOCK PAGE-HEADER*/
.page-header {
    display: flex;
    flex-direction: column;
}

.page-header__level {
    width: 320px;
    margin: 0 auto;

    font-size: 20px;
    line-height: 20px;
}

.page-header__level--1 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: flex-start;
    align-items: center;

    width: 280px;
    height: 66px;
    //min-height: 66px;
}

.page-header__level--2 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    height: 480px;
}

.page-header__level--mobile {
    padding-bottom: 275px;
}

.page-header__logo-link {
    position: relative;
    color: transparent;
}

.page-header__logo-image {
    position: absolute;
    top: -20px;
    left: 0;
    width: 33px;
    height: 38px;
    background: url(../img/logo-mobile.png) no-repeat;
}
.page-header__logo-image:hover {
    opacity: 0.8;
}

.page-header__logo-image:active {
    opacity: 0.6;
}

.page-header__site-name {
    margin-left: 60px;
    font-size: 20px;
    line-height: 20px;
}


/*BLOCK PAGE-HEADER*/



/*BLOCK MAIN-HAV*/

.main-nav__closed .main-nav {
    position: relative;
    width: auto;
}
.main-nav__wrapper {
    display: block;
}

.main-nav__toggle {
    position: relative;
    z-index: 2;

    display: block;
    width: 70px;
    height: 50px;
    background-color: #ffffff;
    border: none;
    cursor: pointer;

    &:hover {
        background-color: #ffffff;
        outline: none;
    }
}

.main-nav__closed .main-nav__toggle::before {
    content: "";

    position: absolute;
    top: 15px;
    right: 0px;

    width: 24px;
    height: 2px;
    cursor: pointer;

    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000,
                0 16px 0 0 #000000;

}

.main-nav__opened .main-nav__toggle {
    &::before {
        content: "";

        position: absolute;
        top: 24px;
        right: 0px;

        width: 24px;
        height: 2px;
        cursor: pointer;
        background-color: #000000;
        transform: rotate(45deg);
    }

    &::after {
        content: "";

        position: absolute;
        top: 24px;
        right: 0px;

        width: 24px;
        height: 2px;
        cursor: pointer;
        background-color: #000000;
        transform: rotate(-45deg);
    }
}


/*BLOCK MAIN-HAV*/



/*BLOCK SITE-LIST*/
.site-list {
    list-style-type: none;
}

.site-list__item {
    display: block;
    padding: 24px 20px;
    font-weight: 400;
    text-transform: uppercase;
    border-top: 1px solid #e6e6e6;
}

.site-list__item a {
    color: #000000;
    text-decoration: none;
}
/*BLOCK SITE-LIST*/



/*BLOCK USER-LIST*/
.user-list {
    display: block;
    list-style-type: none;
}

.user-list__item {
    display: block;
    padding: 24px 20px;
    font-weight: 400;
    text-transform: uppercase;
    border-top: 1px solid #e6e6e6;
}

.user-list__item a {
    color: #000000;
    text-decoration: none;
}
/*BLOCK USER-LIST*/


.feeding {
    padding: 0 20px;
    margin: 0 auto;
    color: #ffffff;
    text-align: center;

    background: url(../img/index-background-mobile.png) no-repeat;
}

.feeding__title {
    font-size: 36px;
    line-height: 36px;
    font-weight: normal;
}

.feeding__slogan {
    font-size: 20px;
    line-height: 20px;
}

.feeding__button {
    width: 100%;
    display: block;
    text-align: center;
}



@media (max-width: 767px) {

    .page-header__level--1 {
        position: relative;
    }
    .main-nav__wrapper {
        position: absolute;

        top: 75px;
        left: -20px;
        width: 320px;
    }
    .main-nav__closed .site-list {
        display: none;
    }
    .main-nav__closed .user-list {
        display: none;
    }

    .main-nav__opened .user-list {
        display: block;
    }

    .main-nav__opened .user-list {
        display: block;
    }

    .main-nav__list {
        margin: 0;
        padding: 0;
        text-align: center;
    }

    .main-nav--nojs .main-nav__toggle {
        display: none;
    }

    .main-nav--nojs .site-list {
        display: block;
    }

    .main-nav--nojs .user-list {
        display: block;
    }

}



@media (min-width: @tablet-width) {

    .page-header {
        padding-top: 20px;
    }

    .page-header__level--1 {
        height: auto;
    }

    .page-header__level {
        width: 708px;
        margin: 0 auto;
        font-size: 18px;
        line-height: 24px;
    }

    .page-header__level--1 {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        width: 708px;
    }

    .page-header__level--2 {
        justify-content: flex-start;
        height: 800px;

        margin-top: 60px;
        background: url(../img/index-can-tablet.jpg) no-repeat -20px 170px;
    }

    .page-header__wrapper {
        width: auto;
    }

    .page-header__site-name {
        display: none;
    }

    .page-header__logo-link {
        position: relative;
    }

    .page-header__logo-image {
        position: absolute;
        top: -30px;
        left: 0;
        width: 173px;
        height: 58px;
        background: url(../img/logo-tablet.png) no-repeat;
    }
    .main-nav__toggle {
        display: none;
    }

    .main-nav__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .user-list {
        display: block;
    }

    .site-list__item {
        display: block;
        margin-right: 36px;
        padding: 0;
        font-weight: 400;
        text-transform: uppercase;
        border-top: 0px solid #e6e6e6;

        &:last-child {
            margin-right: 0;
        }

        &:hover {
            opacity: 0.6;
        }
        &:active {
            opacity: 0.3;
        }

        &--active {
            position: relative;
            &::after {
                content: "";
                position: absolute;
                bottom: -3px;
                right: 0;
                left: 0;
                height: 2px;
                background-color: #68b738;
            }
        }

    }

    .user-list__item {
        display: none;
        padding: 0;
        font-weight: 400;
        text-transform: uppercase;
        border-top: 0px solid #e6e6e6;

        &:hover {
            opacity: 0.6;
        }
        &:active {
            opacity: 0.3;
        }
        &--active {
            position: relative;
            &::after {
                content: "";
                position: absolute;
                bottom: -3px;
                right: 0;
                left: 0;
                height: 2px;
                background-color: #ffffff;
            }
        }
    }



    .feeding {
        width: 427px;
        padding: 0;
        margin-left: 90px;

        color: #000000;
        background: none;
        text-align: left;
    }

    .feeding__title {
        margin: 0;
        padding: 0;

        font-size: 60px;
        line-height: 60px;
    }

    .feeding__logo-image {
        display: none;
    }

    .feeding__button {
        display: block;
        width: 195px;

        padding: 18px 25px;
        font-size: 20px;
        line-height: 20px;
        font-weight: 400;
        text-align: center;
    }
}

@media (min-width: @desktop-width) {
    .page-header--index {
        position: relative;
        width: 1440px;
				padding-top: 50px;
				margin: 0 auto;
    }

    .page-header--index::before {
        position: absolute;
        content: "";
        z-index: 1;
        top: 0;
        right: 0;

        width: 720px;
        height: 696px;
        background: url(../img/index-background-desktop.jpg) no-repeat;
    }
    .page-header__level {
        position: relative;
        z-index: 2;
        width: 1220px;
        margin: 0 auto;
        font-size: 18px;
        line-height: 24px;
    }

    .page-header__level--2 {
        justify-content: flex-start;
        height: 560px;

        margin-top: 60px;
        background: url(../img/index-can.png);
        background-repeat: no-repeat;
        background-position: 500px 0px;
    }

    .page-header__logo-image {
        position: absolute;
        top: -30px;
        left: 0;
        width: 202px;
        height: 59px;
        background: url(../img/logo-desktop.png) no-repeat;
    }

    .feeding {
        margin-top: 80px;
    }

    .site-list .site-list__item {
            & a {
                font-size: 20px;
                line-height: 24px;
            }
        }

    .site-list--desktop .site-list__item {

        & a {
            color: #ffffff;
        }
        &--active::after {
            background-color: #ffffff;
        }
    }
}
