@import 'variables';

.data__big-heading {
    position: relative;
    width: 100%;
}

.data-list {
    display: flex;
    flex-direction: column;

    margin: 0;
    margin-top: 20px;
    padding: 0 20px;

    list-style-type: none;
}


.data-list__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 280px;
    min-height: 45px;
    margin: 0;
    margin-bottom: 14px;
    padding: 0;
}

.data-list__item--1 {
    justify-content: flex-start;
}

.data-list__value {
    display: block;
    margin: 0;
    padding: 0;

    font-size: 16px;
    line-height: 18px;
}

.data-list__field {
    margin: 0;
    height: 45px;
    padding-left: 15px;
    font-family: 'Oswald', sans-serif;
    font-size: 16px;
    line-height: 18px;
    color: #000000;

    border: 1px solid #e7e7e7;

    &:hover {
        border: 1px solid #cdcdcd;
    }

    &:focus {
        border: 2px solid #444444;
    }
}

.data-list__field--1 {
    width: 70%;
}

.data-list__field--2 {
    width: 63%;
}

.data-list__field--3 {
    width: 48%;
}

/*Не могу разместить псевдоэлементом иконки*/
.data-list__field--4 {
    position: relative;
    width: 60%;
    padding-right: 32px;
}

.data-list__field--4::before {
    content: "";
    position: absolute;
    z-index: 10;
    top: 0;
    right: 0;

    width: 17px;
    height: 13px;

    background: url(../img/mail.png) no-repeat;
}

.data-list__field--5 {
    width: 55%;
    padding-right: 32px;
}

.data-list__field--5::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;

    width: 20px;
    height: 20px;
    background: url(../img/mail.png) no-repeat;
}
/* */

.data-list__field-radio {
    display: none;
}

.data-list__field-сheckbox {
    display: none;
}

.data-list__field-radio + label {
    position: relative;
    display: block;

    padding-left: 16px;

    cursor: pointer;
}

.data-list__field-сheckbox  + label {
    position: relative;
    display: block;

    padding-left: 16px;

    cursor: pointer;

}

.data-list__field-radio:checked + label {

    &:active {
        opacity: 0.3;
    }
    &::before {
        content: "";
        position: absolute;
        top: 3px;
        left: -22px;

        width: 12px;
        height: 12px;

        background-color: #68b738;
        border-radius: 50%;
    }
}
.data-list__field-сheckbox:checked + label {
    &:active {
        opacity: 0.3;
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -26px;

        width: 21px;
        height: 16px;

        background: url(../img/tick.png) no-repeat;
    }
}

.data-list__field::placeholder {
    position: relative;
    top: 3px;
    color: #444444;
}

.data-list__field:focus {
    outline: none;
}

.data-list__field:focus::placeholder {
    visibility: hidden;
}

.data-list__btn {
    width: 30px;
    height: 30px;
    border: 1px solid #cfcfcf;
}

.btn--radio {
    border-radius: 50%;
}


.data__comments {
    margin-top: 24px;
    padding: 0 20px;
}

.data__textarea {
    width: 248px;
    height: 150px;
    padding: 12px 15px;

    font-family: 'Oswald', sans-serif;
    font-size: 16px;
    line-height: 18px;
    color: #444444;

    resize: none;
    border: 1px solid #e6e6e6;

}


@media (min-width: @tablet-width) {

    .data-list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        width: 630px;
        height: 195px;
        margin: 0;
        padding: 0;

    }

    .data-list--desktop {
        width: 603px;
        padding-left: 27px;
        justify-content: center;
        border: 1px solid #e6e6e6;
    }

    .data-list--desktop-2 {
        justify-content: center;

        width: 630px;
        height: auto;
        padding: 0;
    }

    .data-list--desktop-2 .data-list__item {
        margin-bottom: 20px;
    }

    .data-list--add {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;

        width: 100%;
    }

    .data-list__item {
        width: 100%;
        margin: 0;
    }

    .data-list--add .data-list__item {
        width: 50%;
        margin: 0;
    }

    .data-list__value {
        font-size: 20px;
        line-height: 20px;
    }

    .data-list__field {
        height: 52px;
        padding-left: 19px;
        font-size: 20px;
        line-height: 20px;
    }

    .data-list__field--1 {
        width: 84%;
    }

    .data-list__field--2 {
        width: 80%;
    }

    .data-list__field--3 {
        width: 72%;
    }

    .data-list__field--4 {
        width: 79%;
    }

    .data-list__field--5 {
        width: 76%;
    }

    .data__big-heading {
        align-self: flex-start;
    }

    .data__comments {
        width: 630px;
        padding: 0;
        margin-top: 60px;
    }

    .data__textarea {
        width: 600px;
        height: 150px;
        padding: 12px 15px;

        font-family: 'Oswald', sans-serif;
        font-size: 16px;
        line-height: 18px;
        color: #444444;

        resize: none;
        border: 1px solid #e6e6e6;

    }

}


@media (min-width: @desktop-width) {
    .data-list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 0;
        width: 570px;
        height: 195px;
        margin: 0;
        padding: 0;

    }

    .data-list--desktop {
        width: 543px;
        padding-left: 27px;
        justify-content: center;
    }

    .data-list--desktop-2 {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 1220px;
        height: auto;
        padding: 0;
    }

    .data-list--desktop-2 .data-list__item {
        width: 570px;
    }

    .data-list__field--4 {
        width: 73%;
    }

    .data-list__field--5 {
        width: 70%;
    }

    .data__comments {
        width: 1220px;
        padding: 0 110px;
    }

    .data__textarea {
        width: 1190px;
    }

    .data-list--add {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;

        width: 100%;
        height: auto;
    }

    .data-list--add .data-list__item {
        width: 25%;
        margin: 0;
    }
}
